<template>  
  <div class="selectdown">
    <el-scrollbar height="800px">
    <el-table  class="table-content"
        :data="dataList"
        border
        style="width: 100%;"
    >
      <el-table-column
      label="序号"
      fixed="left"
      width="50"          
      align="center"
      color="black"
      >
      <template v-slot="scope">
          {{ scope.$index+1 }}
      </template>
      </el-table-column>

      <el-table-column
      label="课程编号"
      align="center"
      color="black"
      sortable
      prop="courseNum" />

      <el-table-column
      label="课程名称"
      align="center"
      color="black"
      prop="courseName" />

      <el-table-column
      label="课程教师"
      align="center"
      color="black"
      sortable
      prop="courseteacher" />

      <el-table-column
      label="课程描述"
      align="center"
      color="black"
      sortable
      prop="courseinfo" />

      <el-table-column
      label="课程上课日"
      align="center"
      color="black"
      sortable
      prop="courseday" />

      <el-table-column
      label="课程上课节次"
      align="center"
      color="black"
      sortable
      prop="courseorder" />

      <el-table-column
      label="课程学分"
      align="center"
      color="black"
      sortable
      prop="coursescore" />

      <el-table-column
      label="课程选课结束时间"
      align="center"
      color="black"
      sortable
      prop="courseselectend" />

      <el-table-column
          label="操作"
          align="center"
          color="black"
      >
      <template v-slot="scope">
        <el-button type="primary" size="mini" @click="studentname(scope.row.courseNum)">学生点名册</el-button>
      </template>
      </el-table-column>
    </el-table>
    </el-scrollbar>
  </div>

  <el-dialog title="点名册" v-model="studentnameVisible" width="40%">
    <el-table  class="table-studentname"
      :data="dataList1"
      border
      style="width:100%;"
    >
      <el-table-column
      label="序号"
      fixed="left"
      width="200px"          
      align="center"
      color="black"
      >
      <template v-slot="scope">
          {{ scope.$index+1 }}
      </template>
      </el-table-column>

      <el-table-column
      label="学生学号"
      align="center"
      color="black"
      width="300px" 
      prop="userNum" />

      <el-table-column
      label="学生姓名"
      align="center"
      color="black"
      width="200px" 
      prop="userName" />
    </el-table>
    <span  class="dialog-footer">
      <el-button @click="studentnameVisible = false">关闭</el-button>
    </span>
  </el-dialog>
</template>

<script>
import {getmyCourseList,getstudentnameList} from '@/service/genServ.js'
export default {
  name: 'mycourse',
  data() {
    return {
      studentnameVisible : false,
      dataList:[],
      dataList1:[]
    }
  },
  created() {
    this.doQuery();
  },
  methods: {
    doQuery() {
       getmyCourseList({'courseteacher':localStorage.getItem('username')}).then(res => {
        this.dataList = res.data;
      })
    },

    studentname(courseNum) {
      getstudentnameList({'courseNum':courseNum}).then(res => {
        this.dataList1 = res.data;
        console.log(this.dataList1);
        this.studentnameVisible = true;
      })
    },
   
  }
}
</script>

<style scoped>
.selectdown{
  width: 1650px;
  position:absolute;
  top:100px;
  left:225px;
}
</style>
